import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styles from './CategorySlider.module.css';
import Slider from "react-slick";
import axios from 'axios';

export default function CategorySlider()  {
    const [isloding, setisloding] = useState(false);
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 7,
        slidesToScroll: 1,
        arrows:false,
        autoplay:true,

    };
    const [categories, setcategories] = useState([]);
    async function getcategories(){
        setisloding(true)
 let {data} = await  axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
 setcategories(data.data)
 setisloding(false)
     }
     useEffect(()=>{
        getcategories()
     },[])
    return <>
          {isloding ? <div className='layar w-100 h-100 position-absolute top-0 end-0 bottom-0 start-0 '>
          <div className='position-absolute end-50 top-50  loding'>
         <div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>

      </div>
          </div> : ''}
    <Slider { ...settings} autoplaySpeed={1000}>
{categories.map((category)=> <div key={category._id}>
<img className='w-100' height={200}  src={category.image} alt="" />
</div>)}
    </Slider>

    </>
}


